app.controller("starListCtrl",function($ionicScrollDelegate,$baseurl,$scope,$localStorage,$http,$loading,$location){
	function init(){
		 $scope.doc=$localStorage.doctor;
		 $scope.user=$localStorage.user;
		 getListStarUser();
	}
	 //获取关注用户
  function getListStarUser() {
    $loading.show();
     $http({
      method: 'post',
      url: $baseurl + '/medicalsys/userfollower/user2studio/getUser2StudioList?studioId=' + $scope.doc.studioId,
    }).success(function(data) {
    $loading.hide();
      if (data.success) {
      	  $scope.userlist=data.roots;
	       $ionicScrollDelegate.$getByHandle('listScroll').scrollTop();
      } else {
         ZENG.msgbox.show(data.errorMessage||"出错了",3,1500);
      }
      $loading.hide();
    }).error(function(data) {
    	$loading.hide();
      ZENG.msgbox.show("出错了....", 5, 1500);
    });
  }

  $scope.goKaifang=function(user){
    $localStorage.patient ={
         phone:user.userAccount
     }
     $location.path("app/kaifang");
  }
  init();
});