app.controller("recordCtrl",function($loading,$scope,$localStorage,$filter,$http,$location,$baseurl){
	  function init(){
	  	 $scope.doc=$localStorage.doctor;
	    $scope.user=$localStorage.user;
	    getList(1);
	  }
	  function getList(_page,_filter){
	  	var page= parseInt(_page)||1;
	  	var now = new Date();
        var today = $filter('date')(now, 'yyyy-MM-dd');
        var startDate = '2017-03-01';
        var drdate = now.getTime();
        var limit = 20;
        var start = limit * (page - 1);
         $scope.orderList=$scope.orderList||[];
         $loading.show();
	     $http({
                method: 'get',
                url:$baseurl + '/medicalsys/orders/orderinfo/getAppOrderPageList?type=0&userType=1&_dc=' + drdate + '&start=' + start + '&limit=' + limit + 
            '&userId=' + $scope.user.id + '&startTime=' + startDate + '&endTime=' + today
            }).success(function (data) {
                if (data.success) {
                    var data = data.roots;
                    $scope.orderList = $scope.orderList.concat(data);
                    $scope.$broadcast('scroll.infiniteScrollComplete');
                } else {
                    ZENG.msgbox.show(data.errorMessage||'出错了', 3, 1500);
                }
                 $loading.hide();
            }).error(function () {
                ZENG.msgbox.show("系统出错", 5, 1500);
                $loading.hide();
            });
	  }
     init();
});