 app.config(function($stateProvider, $urlRouterProvider) {

   $stateProvider
     .state('app', {
       url: '/app',
       abstract: true,
       templateUrl: 'tpl/app.html'
     })
     .state('app.login', {
       url: '/login',
       views: {
         'menuContent': {
           templateUrl: 'tpl/login.html',
           controller: 'loginCtrl'
         }
       }
     })
     .state('app.main', {
       url: '/main',
       views: {
         'menuContent': {
           templateUrl: 'tpl/main.html',
           controller: 'mainCtrl'
         }
       }
     })
     .state('app.yuyueInfo', {
       url: '/yuyueInfo',
       views: {
         'menuContent': {
           templateUrl: 'tpl/yuyueInfo.html',
           controller: 'yuyueInfoCtrl'
         }
       }
     })

     .state('app.paiban', {
       url: '/paiban',
       views: {
         'menuContent': {
           templateUrl: 'tpl/paiban.html',
           controller: 'paibanCtrl'
         }
       },
       cache:false,
     })
      .state('app.starList', {
       url: '/starList',
       views: {
         'menuContent': {
           templateUrl: 'tpl/starList.html',
           controller: 'starListCtrl'
         }
       },
     })
      .state('app.kaifang', {
       url: '/kaifang',
       views: {
         'menuContent': {
           templateUrl: 'tpl/kaifang.html',
           controller: 'kaifangCtrl'
         }
       },
     })
      .state('app.record', {
       url: '/record',
       views: {
         'menuContent': {
           templateUrl: 'tpl/record.html',
           controller: 'recordCtrl'
         }
       },
     })
     .state('app.drlist', {
       url: '/drlist',
       views: {
         'menuContent': {
           templateUrl: 'tpl/drlist.html',
           controller: 'drlistCtrl'
         }
       },
     })
     .state('app.chat', {
       url: '/chat',
       views: {
         'menuContent': {
           templateUrl: 'tpl/chat.html',
           controller: 'chatCtrl'
         }
       },
       cache:false,
     })
     .state('app.person', {
       url: '/person',
       views: {
         'menuContent': {
           templateUrl: 'tpl/person.html',
           controller: 'personCtrl'
         }
       },
     })
     .state('app.addChufang', {
       url: '/addChufang',
       views: {
         'menuContent': {
           templateUrl: 'tpl/addChufang.html',
           controller: 'addChufangCtrl'
         }
       },
     })
     .state('app.circle', {
       url: '/circle',
       views: {
         'menuContent': {
           templateUrl: 'tpl/circle.html',
           controller: 'circleCtrl'
         }
       },
     })
     .state('app.recordDetail', {
       url: '/recordDetail',
       views: {
         'menuContent': {
           templateUrl: 'tpl/recordDetail.html',
           controller: 'recordDetailCtrl'
         }
       },
     })
   $urlRouterProvider.otherwise('/app/main');
 });
