    app.factory('getage',function(){
        return{
            Age:function(strBirthday){
                var returnAge;
                if(typeof(strBirthday)=='string'){
                    var strBirthdayArr=strBirthday.split("-");
                    var birthYear = strBirthdayArr[0];
                    var birthMonth = strBirthdayArr[1];
                    var birthDay = strBirthdayArr[2];
                }else if(typeof(strBirthday)=='number'){
                    var birthYear = new Date(strBirthday).getFullYear();
                    var birthMonth = new Date(strBirthday).getMonth()+1;
                    var birthDay = new Date(strBirthday).getDate();
                }
                d = new Date();
                var nowYear = d.getFullYear();
                var nowMonth = d.getMonth() + 1;
                var nowDay = d.getDate();

                if(nowYear == birthYear){
                    returnAge = 0;//同年 则为0岁
                }else{
                    var ageDiff = nowYear - birthYear ; //年之差
                    if(ageDiff > 0){
                        if(nowMonth == birthMonth) {
                            var dayDiff = nowDay - birthDay;//日之差
                            if(dayDiff < 0){
                                returnAge = ageDiff - 1;
                            }else{
                                returnAge = ageDiff ;
                            }
                        }
                        else{
                            var monthDiff = nowMonth - birthMonth;//月之差
                            if(monthDiff < 0){
                                returnAge = ageDiff - 1;
                            }else{
                                returnAge = ageDiff ;
                            }
                        }
                    }else{
                        returnAge = -1;//返回-1 表示出生日期输入错误 晚于今天
                    }
                }

                return returnAge;//返回周岁年龄

            }
        }
    });
     app.factory('locals', ['$window', function ($window) {
        return { //存储单个属性
            set: function (key, value) {
                $window.localStorage[key] = value;
            }, //读取单个属性
            get: function (key, defaultValue) {
                return $window.localStorage[key] || defaultValue;
            }, //存储对象，以JSON格式存储

            setObject: function (key, value) {
                $window.localStorage[key] = JSON.stringify(value);
            }, //读取对象
            getObject: function (key) {
                return JSON.parse($window.localStorage[key] || '[]');
            },
            getObjecta: function (key) {
                return JSON.parse($window.localStorage[key] || '{}');
            },
            del: function (key) {
                $window.localStorage.removeItem[key];
            },
            delall: function () {
                $window.localStorage.clear();
            }

        }
    }])