app.controller("loginCtrl", function($scope, $http, $baseurl,$loading,$location,$localStorage) {
  function init(){
  	$scope.formData = {
	  	userType:1,
	  	force:true,
	  };
	 var accountId=$localStorage.user&&$localStorage.user.accountId;
	 $scope.formData.username=parseInt(accountId)||undefined;
  }
  $scope.doLogin = function() {
  	$loading.show(true,"登录中....");
    $http({
      url: $baseurl + '/medicalsys/login',
      data:$.param($scope.formData),
       timeout: 6000,
       headers: {'Content-Type': 'application/x-www-form-urlencoded'},
       method: "post",
       timeout: 6000,
    }).success(function(data) {
    	if(data.success){
    		$localStorage.user=data.data;
        getDoctor();
    	}else{
        $loading.hide();
    		ZENG.msgbox.show(data.errorMessage||"登录失败",3,1500);
    	}
    }).error(function(response) {
    	console.log(response);
    	$loading.hide();
    	ZENG.msgbox.show('出错了...', 5, 1500);
    });
  }

 function getDoctor(){
        $http({
            method: 'POST',
            timeout: 6000,
            url: $baseurl+'/medicalsys/doctors/doctorInfo/getDoctorByUser',
        }).success(function(data) {
            if (data.success) {
                $localStorage.doctor=data.roots[0];
                getStudioList();
            } else {
                 $loading.hide();
                 ZENG.msgbox.show(data.errorMessage||"出错了",3,1500);
            }
        }).error(function(data) {
            $loading.hide();
            ZENG.msg.show("出错了...", 5,1500);
        });
    }
  function getStudioList(){
    $http({
            method: 'get',
            url: $baseurl+'/medicalsys/doctors/studio/getStudioList',
        }).success(function (data) {
            if(data.success){
               $localStorage.studioList=data.list;
               $loading.hide();
               $location.path("/app/main");
             }else{
                 $loading.hide();
                 ZENG.msgbox.show(data.errorMessage||"出错了",3,1500);
             }
        }).error(function (data) {
             $loading.hide();
             ZENG.msgbox.show('出错了...',1,1500);
        })
  }

  init();
});
