app.controller("drlistCtrl", function($rootScope,$location,$ionicScrollDelegate,$loading,$baseurl, $scope, $http,$sessionStorage,$localStorage) {
  //切换显示全部,在线,未回复
  $scope.toggleShow = function(item) {
    $scope.showItem=item;
    $sessionStorage.showItem=item;
    if (item == 1) getListAllUser();
    else if (item == 2) getListStarUser();
    else if (item == 3) getListOnlineUser();
    else if (item == 4) getListNoreadUser();
  }
  
  //获取所有用户
  function getListAllUser() {
    var doctor = $localStorage.doctor;
    $loading.show();
     $http({
      method: 'GET',
      url: $baseurl + '/medicalsys/webchat/chatmsg/getUserChatMsgByStudio?recvid=' + doctor.studioId,
    }).success(function(data) {
    $loading.hide();
      if (data.success) {
        $scope.list_user = data.roots;
        $ionicScrollDelegate.$getByHandle('listScroll').scrollTop();

      } else {
        ZENG.msgbox.show(data.errorMessage||"出错了", 3, 1500);
      }
    }).error(function(data) {
    	$loading.hide();
      ZENG.msgbox.show("出错了...", 5, 1500);
      ZENG.ms
    });
  }

  //获取关注用户
  function getListStarUser() {
    var doctor = $localStorage.doctor;
    $loading.show();
     $http({
      method: 'post',
      url: $baseurl + '/medicalsys/userfollower/user2studio/getUser2StudioList?studioId=' + doctor.studioId,
    }).success(function(data) {
    $loading.hide();
      if (data.success) {
      	 $scope.list_user = data.roots;
      	 for (var i in $scope.list_user) {
	        var item = $scope.list_user[i];
	        item['sender'] = item.userName;
	        item['nickname'] = item.userName;
	        item['headerUrl'] = item.headimgurl;
	        item['accountId']=item.userAccount;
          item['id']=item.userId;
	      }
	       $ionicScrollDelegate.$getByHandle('listScroll').scrollTop();
      } else {
         ZENG.msgbox.show(data.errorMessage||"出错了",3,1500);
      }
    }).error(function(data) {
    	$loading.hide();
      ZENG.msgbox.show("出错了...", 5, 1500);
    });
  }

  //获取在线用户
  function getListOnlineUser() {
    var doctor = $localStorage.doctor;
    $loading.show();
     $http({
      method: 'POST',
      url: $baseurl + '/medicalsys/sys/user/getWxUserInfoOnline?studioId=' + doctor.studioId,
    }).success(function(data) {
    $loading.hide();
      for (var i in data) {
        var item = data[i];
        item['sender'] = item.nickname;
      }
      $scope.list_user = data;
       $ionicScrollDelegate.$getByHandle('listScroll').scrollTop();
    }).error(function(data) {
    	$loading.hide();
      ZENG.msgbox.show("出错了...", 5, 1500);
    });
  }

  // 获取未读消息的用户
  function getListNoreadUser() {

    var doctor = $localStorage.doctor;
    $loading.show();
     $http({
      method: 'GET',
      url: $baseurl + '/medicalsys/webchat/chatmsg/getUserChatMsgByStudio?recvid=' + doctor.studioId,
    }).success(function(data) {
    $loading.hide();
      if (data.success) {
        var list_user_all = data.roots;
        var users=[];
        for (var i in list_user_all) {
          if (list_user_all[i].nums > 0) {
            users.push(list_user_all[i])
          }
        }
        $scope.list_user=users;
         $ionicScrollDelegate.$getByHandle('listScroll').scrollTop();
      } else {
         ZENG.msgbox.show(data.errorMessage||"出错了",3,1500);
      }
    }).error(function(data) {
    	$loading.hide();
        ZENG.msgbox.show("出错了...", 5, 1500);
    });
  }

  $scope.gochatByUser=function(user){
    $localStorage.chatuser=user;
    $location.path("/app/chat");
  }

   $scope.swipe=function(offset){
      var tmp=$scope.showItem+offset;
      if(tmp>4) $scope.showItem=1;
      else if(tmp<1) $scope.showItem=4;
      else $scope.showItem=tmp;    
      $scope.toggleShow($scope.showItem);
    }
 function init() {
    $scope.showItem=$sessionStorage.showItem||4;
    $scope.toggleShow($scope.showItem);
    $rootScope.getNotReadMessList();
  }
    init();
});
