app.controller("addChufangCtrl", function($ionicScrollDelegate,$ionicModal,$filter,$confirm,$ionicPopup, $sessionStorage, $ionicScrollDelegate, $baseurl, $scope, $localStorage, $http, $loading, $location) {
    function init() {
        $scope.doctor = $localStorage.doctor;
        $scope.user = $localStorage.user;
        $scope.medicalList1=[];
        $scope.medicalList2=[];
        $scope.medicalList3=[];
        $scope.comments = ["无","焗服", "包煎", "炒", "冲服", "打碎", "后下", "化服", "另包", "另炒", "吞服", "外洗", "先煎","烊化"];
        $scope.showItem = $sessionStorage.showItem || 1; //中药饮片:83/中成药:73/ 理疗:1
        $scope.showIndex=$sessionStorage.showItem || 1; 
        initModal();
    }
    $scope.toggleShow = function(item) {
        $sessionStorage.showItem = item;
        $scope.showItem = item;
    }
     $scope.toggleIndex = function(tabIndex) {
        $sessionStorage.showIndex = tabIndex;
        $scope.showIndex = tabIndex;
        $scope.pid='f8cc1dc0626bca600162748657931d1c';
        $scope.times=2;
        $ionicScrollDelegate.$getByHandle('listScroll2').scrollTop();
        if (tabIndex == 3) {
             if($scope.zcMedicals1!=undefined) return;
             $scope.isUp3=[];
             $scope.medicalDetails3=[];
             //获取致诚金方药方;
             $loading.show();
             $http({
                 method: 'post',
                 timeout:4000,
                 url: $baseurl + '/medicalsys/mzsys/getPattern2?deptSn=593&doctorCode=' + $scope.doctor.hisId + '&flag=1'
             }).success(function(data) {
                 $scope.zcMedicals1 = data.roots;
                 $scope.isUp3.push(false);
                 $scope.medicalDetails3.push([]);
                 $loading.hide();
             }).error(function(){ $loading.hide();});
         } else if (tabIndex == 4) {
             if($scope.zcMedicals2!=undefined) return;
             $scope.isUp4=[];
             $scope.medicalDetails4=[];
             //获取个人药方;
             $http({
                 method: 'post',
                 url: $baseurl + '/medicalsys/mzsys/getPattern2?deptSn=593&doctorCode=' + $scope.doctor.hisId + '&flag=3'
             }).success(function(data) {
                 $scope.zcMedicals2 = data.roots;
                 $scope.isUp4.push(false);
                 $scope.medicalDetails4.push([]);
             });
         } else if (tabIndex == 1) {
             if($scope.preLists!=undefined) return;
             $scope.isUp1=[];
             $scope.medicalDetails1=[];
             // 获取上次看诊已开处方
             $http({
                 method: 'get',
                 url: $baseurl + '/medicalsys/mzsys/getPrescriptionList?flag=1&pid=' + $scope.pid + '&times=' + ($scope.times - 1)
             }).success(function(data) {
                 if (data) {
                     var objcf = data.list0.preList;
                     $scope.preLists = eval('(' + objcf + ')');
                     for (var i in $scope.preLists) {
                         var item = $scope.preLists[i];
                         var index = parseInt(item['prescriptionSn']) - 1;
                         item['name'] = "处 方 " + index;
                         $scope.isUp1.push(false);
                         $scope.medicalDetails1.push([]);
                     }
                    console.log("$scope.preLists",$scope.preLists);
                 }
             })


         } else if (tabIndex == 2) {
             if($scope.personalYF!=undefined) return;
             $http({
                 method: 'post',
                 url: $baseurl + '/medicalsys/mzsys/getDoctorYP?doctorCode=' + $scope.doctor.hisId
             }).success(function(data) {
                 for (var i in data.roots) {
                     $http({
                         method: 'get',
                         url: $baseurl + '/medicalsys/mzsys/getChargeItem?sValue=' + data.roots[i].chargeCode
                     }).success(function(data) {
                         data.roots[0]['dosage'] = 5;
                         if ($scope.personalYF == undefined) $scope.personalYF = [];
                         $scope.personalYF.push(data.roots[0]);
                     });
                 }
             });
         }
    }
    $scope.toggle_1=function(pre,index){
        $scope.isUp1[index]=!$scope.isUp1[index];
        change_1(pre,index);
    }

    function change_1(pre,index) {
        if($scope.medicalDetails1[index].length>0) return;
         $http({
             method: 'get',
             url: $baseurl + '/medicalsys/mzsys/getPrescriptionDetailList?pid=' + pre.pid + '&times=' + pre.times + '&accountSn=0&prescriptionSn=' + pre.prescriptionSn + '&flag=2&dataType=0'
         }).success(function(data) {
             var tmpData = JSON.parse(data.list);
             $scope.medicalDetails1[index]=tmpData;
             console.log("$scope.medicalDetails1",$scope.medicalDetails1);
         });
     }

    $scope.toggle_3=function(medical,index){
        $scope.isUp3[index]=!$scope.isUp3[index];
        change_3(medical,index);
    }

    function change_3(medical,index) {
         $http({
             method: 'get',
             url: $baseurl + '/medicalsys/mzsys/getPatternDetail?tsn=' + medical.tsn
         }).success(function(data) {
             $scope.medicalDetails3[index] = data.roots;
         });
     }

   $scope.toggle_4=function(medical,index){
        $scope.isUp4[index]=!$scope.isUp4[index];
        change_4(medical,index);
    }

    function change_4(medical,index) {
         $http({
             method: 'get',
             url: $baseurl + '/medicalsys/mzsys/getPatternDetail?tsn=' + medical.tsn
         }).success(function(data) {
             $scope.medicalDetails4[index] = data.roots;
         });
     }

     
    $scope.searchMedical = function() {
        if ($scope.medicalNameKey.length >= 2 || $scope.medicalNameKey.charCodeAt() > 255) {
            $http({
                method: 'get',
                url: $baseurl + '/medicalsys/mzsys/getChargeItem?sValue=' + $scope.medicalNameKey.toUpperCase()
            }).success(function(data) {
                $scope.searchist = [];
                for (var i in data.roots) {
                    if ($scope.showItem == 1 && data.roots[i].groupNo == 83) {
                        $scope.searchist.push(data.roots[i])
                    } else if ($scope.showItem == 2 && data.roots[i].groupNo == 73) {
                        $scope.searchist.push(data.roots[i])
                    } else if ($scope.showItem == 3 && data.roots[i].groupNo == 1) {
                        $scope.searchist.push(data.roots[i])
                    }
                }

            });
        }
    }
    $scope.clearInput = function() {
        $scope.medicalNameKey = undefined;
        $scope.searchist = undefined;
    }
    $scope.choseYc=function(yc){
    	if($scope.showItem==1){
    		$scope.addOrUpdateCF(yc);
    	}
        else if($scope.showItem==2){
        	yc.dosage=1;
        	var flag=true;//是否可以添加饮片
        	for(var i in $scope.medicalList2){
        		 if($scope.medicalList2[i].chargeCode==yc.chargeCode){
        		 	flag=false;
        		 }
        	}
        	if(flag){$scope.medicalList2.push(yc);}
        	else { ZENG.msgbox.show("该中成药已添加!",3,1000)}
        }
        else if($scope.showItem==3){
        	var flag=true;//是否可以添加饮片
        	for(var i in $scope.medicalList3){
        		 if($scope.medicalList3[i].chargeCode==yc.chargeCode){
        		 	flag=false;
        		 }
        	}
        	if(flag){$scope.medicalList3.push(yc);}
        	else { ZENG.msgbox.show("该诊疗已添加!",3,1000)}
        }
    }
    // index undefined 判断是添加还是修改饮片
    $scope.addOrUpdateCF = function(yc,index) {
        if (yc.dosage == undefined) yc.dosage = 5;
        yc.dosage=parseInt(yc.dosage);
        if(yc.drugFlag==1) var subtitle="该药材具有毒性,请谨慎使用";
        $scope.$selectedyc=angular.copy(yc);
        var btnText=(index==undefined)?'添加':'修改';
        var popup = $ionicPopup.show({
            templateUrl: "medicalTpl.html",
            title:yc.chargeName,
            subTitle: subtitle||'',
            scope: $scope,
            buttons: [{
                text: '取消',
                type: 'button-assertive',
            }, {
                text: btnText,
                type: 'button-positive',
                onTap: function(e) { return 1; }
            }, ]
        });
        popup.then(function(res) {
            if (res) {
            	//
            	if(index==undefined){
            		var flag=true;//是否可以添加饮片
	            	for(var i in $scope.medicalList1){
	            		 if($scope.medicalList1[i].chargeCode==$scope.$selectedyc.chargeCode){
	            		 	flag=false;
	            		 }
	            	}
	            	if(flag){$scope.medicalList1.push($scope.$selectedyc);}
	            	else { ZENG.msgbox.show("该饮片已添加!",3,1000)}
            	}else{
            		$scope.medicalList1[index]=$scope.$selectedyc;
            	}

            	console.log($scope.medicalList1);
            }
        })
    }
    
    $scope.chooseVal=function(val){
    	$scope.$selectedyc.dosage=val;
    }
    $scope.addYCNum=function(offset){
    	 var tmp=parseInt($scope.$selectedyc.dosage+offset);
    	 if(isNaN(tmp)||tmp<=0) {$scope.$selectedyc.dosage=1}
    	 else $scope.$selectedyc.dosage=tmp;
    }
   
   //删除一个饮片
     $scope.deleteItem = function(index) {
     	$confirm.show({title:"确定要删除该饮片吗?",content:""},
           function(){
              $scope.medicalList1.splice(index, 1);
            });
     	
     }

    //删除一个中成方
    $scope.deleteZhongcheng = function(index) {
     	$confirm.show({title:"确定要删除吗?",content:""},
           function(){
              $scope.medicalList2.splice(index, 1);
            });
     }

    //删除一个诊疗
    $scope.deleteZhenliao = function(index) {
     	$confirm.show({title:"确定要删除吗?",content:""},
           function(){
              $scope.medicalList3.splice(index, 1);
            });
     }

    $scope.addZhongchngeNum=function(vo,offset){
    	 var tmp=vo.dosage+offset;
    	 if(isNaN(tmp)||tmp<0) vo.dosage=1;
    	 else vo.dosage=tmp;
    }

 function initModal(){
     $ionicModal.fromTemplateUrl('toolTpl.html', {
         scope: $scope
      }).then(function(modal) {
         $scope.modal = modal;
      });
    }
 $scope.openToolBox=function(){
    $scope.modal.show();
    $scope.toggleIndex( $scope.showIndex);
 }


 $scope.searchCF=function (vo) {
            for (var i in $scope.zcMedicals1) {
                if(!vo) {$scope.zcMedicals1[i]['unshow']=false;continue;}
                if ($scope.zcMedicals1[i].name.indexOf(vo)< 0)
                    $scope.zcMedicals1[i]['unshow']=true;
                else{
                    $scope.zcMedicals1[i]['unshow']=false;
                }
            }
        }

 $scope.addCF_1_2=function(_vo){
  $confirm.show({title:"确定要添加吗?",content:"已选饮片将会被覆盖"},
    function(){
             $scope.medicalList1=[];
                 for(var i in _vo){
                    if(_vo[i].groupNo==83){
                         $scope.medicalList1.push(_vo[i]);
                    }
                 }
              $scope.modal.hide();
           });
 }

 $scope.addCF_3_4=function(medical){
   $http({
         method: 'get',
         url: $baseurl + '/medicalsys/mzsys/getPatternDetail?tsn=' + medical.tsn
     }).success(function(data) {
        var title='确定要添加 【'+medical.name+'】 吗?';
        var list=[],content='';
        for(var i in data.roots){
                if(data.roots[i].groupNo==83){
                      data.roots[i].dosage=$filter('number')(data.roots[i].dosage,1);
                      list.push(data.roots[i]);
                      content+=(data.roots[i].chargeName+'('+data.roots[i].dosage+'g) ');
                }
              }
        $confirm.show({title:title,content:content},
          function(){
              $scope.medicalList1=list;
             $scope.modal.hide();
          });
     });
 }
    init();
})